.layout_container {
    display: flex;
    justify-content: space-between;
}

.admin_container {
    width: 100%;
    padding: 40px 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 70px;
}

.admin_container_heading {
    font-size: 40px;
    font-weight: bold;
    -webkit-text-stroke: 1px #fff;
    /* text-stroke: 2px black; */
    color: transparent;
    letter-spacing: 3px;
    margin-bottom: 20px;
    margin-top: 40px;
    font-family: 'Sono', sans-serif;
}

.add_container {
    background-color: #000;
    padding: 20px 40px;
    border-radius: 3px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* box-shadow: 5px 5px 10px 1px #c1c1c1, 3px 3px 5px 0px #b8b8b8 inset; */
}

.form_heading {
    color: #fff;
    font-size: 20px;
    margin-bottom: 10px;
}

.add_container input {
    border: none;
    border-radius: 3px;
    padding: 5px 8px;
    margin: 10px 0px;
    display: block;
    transition: all 0.8s ease;
}

.add_container input:focus,
.add_container textarea:focus,
.add_container select:focus {
    background-color: #ffceb4;
}

.add_container select:hover {
    cursor: pointer;
}

.add_container input[type="text"] {
    width: 200px;
}

#file-upload {
    display: none;
}

.add_container input[type="submit"] {
    transition: all 0.5s ease;
}

.add_container input[type="submit"]:hover {
    background-color: #FD2155;
    cursor: pointer;
    color: #fff;
}

.add_container input[name="startDate"],
.add_container input[name="endDate"] {
    display: inline-block;
    width: 95px;
    margin-top: 0px;
}

.add_container input[name="startDate"] {
    margin-right: 10px;
}

.add_container textarea {
    display: block;
    width: 200px;
    border: none;
    border-radius: 3px;
    padding: 5px 8px;
}

.add_container select {
    width: 200px;
    border: none;
    border-radius: 3px;
    display: block;
    margin: 10px 0px;
    padding: 2px 8px;
}

.display_heading {
    margin-top: 40px;
    font-size: 25px;
    color: #fff;
    font-weight: 700;
}

.cards_display_container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.card_item {
    width: 280px;
    background-color: #161623a4;
    margin: 10px;
    padding: 8px 0px;
    padding-top: 0px;
    color: #fff;
    border-radius: 15px;
    box-shadow: 20px 20px 50px #00000080;
    border-top: 1px solid #ffffff80;
    border-left: 1px solid rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(5px);
    position: relative;
    padding-bottom: 30px;
    height: 370px;
    cursor: default;
    user-select: none;
}

.custom-file-upload {
    border: 1px solid #ccc;
    display: inline-block;
    padding: 6px 12px;
    cursor: pointer;
    border-radius: 4px;
    font-size: 14px;
    font-weight: bold;
    color: #000;
    background-color: #fff;
    text-align: center;
    margin-top: 0px;
    width: 95px;
    transition: 0.5s ease;
}

.upload_btn {
    border: 1px solid #ccc;
    display: inline-block;
    padding: 6px 12px;
    cursor: pointer;
    border-radius: 4px;
    font-size: 14px;
    font-weight: bold;
    color: #000;
    background-color: #fff;
    text-align: center;
    margin-top: 0px;
    width: 95px;
    transition: 0.5s ease;
    margin-left: 10px;
}

.upload_btn:hover {
    background-color: #06C5A9;
}

.custom-file-upload:hover {
    background-color: #06C5A9;
}

.custom-file-upload i {
    margin-right: 5px;
}

.card_top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 0px;
    background-color: #161623;
    border-top-right-radius: 15px;
    border-top-left-radius: 15px;
    padding: 8px 12px;
    flex-direction: column;
    border-bottom-left-radius: 30px;
    border-bottom-right-radius: 30px;
    margin-bottom: 10px;
}

.card_top .card_head {
    width: 250px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    text-transform: capitalize;
    font-size: 20px;
    margin-bottom: 0px;
}

.card_btns {
    display: flex;
    align-items: center;
    background-color: #ff3b0f;
    width: fit-content;
    padding: 8px 5px;
    color: #fff;
    margin: auto;
    margin-top: 10px;
    margin-bottom: 10px;
    border-radius: 3px;
    cursor: pointer;
}

.card_btns .card_btn {
    padding: 0;
    margin: 0px 3px;
    display: flex;
    border: none;
    cursor: pointer;
    background-color: transparent;
}

.card_btns .delete {
    font-size: 18px;
}

.card_img {
    width: 100%;
    height: 150px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 5px;
    margin-bottom: 15px;
}

.card_proj {
    padding: 0px 15px;
    height: 90px;
    overflow-y: auto;
}

.card_proj::-webkit-scrollbar {
    width: 8px;
    /* width of the scrollbar */
}

.card_proj::-webkit-scrollbar-track {
    background-color: #f2f2f2;
    /* color of the track */
}

.card_proj::-webkit-scrollbar-thumb {
    background-color: #ccc;
    /* color of the thumb */
}

.card_proj::-webkit-scrollbar-thumb:hover {
    background-color: #999;
    /* color of the thumb on hover */
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    /* shadow on hover */
    cursor: pointer;
}

.card_middle {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 0px;
    margin-bottom: 10px;
}

.card_jobtype {
    background-color: #EEA47F;
    padding: 2px 5px;
    border-radius: 3px;
}

.card_main {
    background-color: #fff;
    color: #464646;
    min-height: 46%;
    border-radius: 3px;
    padding: 8px 10px;
    width: 90%;
    margin: auto;
}

.card_job_dates {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    margin: auto;
}

.cardtm {
    margin-top: 0px;
    margin-bottom: 10px;
}

.close {
    position: absolute;
    top: -15px;
    background-color: #ff3b0f;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    padding: 8px;
    right: -15px;
    cursor: pointer;
}

.card_btm_btns {
    padding: 0px 15px;
    margin-top: 15px;
}

.card_btm_btns button {
    width: 48%;
    background-color: #FD2155;
    border: none;
    padding: 8px 12px;
    cursor: pointer;
    transition: all 0.5s ease;
}

.card_btm_btns button:hover {
    opacity: 0.8;
}

.card_btm_btns .edit {
    margin-right: 5px;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}

.card_btm_btns .check {
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}

hr {
    width: 80%;
    height: 0.5px;
    margin: auto;
    border: 0.1px solid rgba(179, 179, 179, 0.667);
    margin-top: 50px;
}

.skill_item {
    width: 280px;
    background-color: #161623a4;
    margin: 10px;
    padding: 8px 0px;
    padding-top: 0px;
    color: #fff;
    border-radius: 15px;
    box-shadow: 20px 20px 50px #00000080;
    border-top: 1px solid #ffffff80;
    border-left: 1px solid rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(5px);
    position: relative;
    padding-bottom: 30px;
}

.skill_bar {
    width: 90%;
    margin: auto;
    height: 20px;
    border-radius: 20px;
    background-color: #949393;
    margin-top: 20px;
    overflow: hidden;
}

.skill_done {
    background-color: #ffceb4;
    height: 20px;
}

.proj_img_div {
    width: 100%;
    height: 150px;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.project_heading {
    font-size: 25px;
    padding: 10px 20px;
}

.project_date {
    font-size: 12px;
    padding: 0px 20px;
    color: #8d8d8d;
}

.project_desc {
    padding: 10px 20px;
    overflow-y: auto;
    height: 100px;
}

.project_desc::-webkit-scrollbar {
    width: 8px;
    /* width of the scrollbar */
}

.project_desc::-webkit-scrollbar-track {
    background-color: #f2f2f2;
    /* color of the track */
}

.project_desc::-webkit-scrollbar-thumb {
    background-color: #ccc;
    /* color of the thumb */
}

.project_desc::-webkit-scrollbar-thumb:hover {
    background-color: #999;
    /* color of the thumb on hover */
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    /* shadow on hover */
    cursor: pointer;
}

@media screen and (min-width: 550px) {

    .admin_container_heading {
        font-size: 55px;
    }
}

@media screen and (min-width: 800px) {

    .admin_container_heading {
        margin-top: 0px;
        font-size: 76px;
    }
}