.sidebar {
    background-color: #000;
    /* height: 100vh; */
    width: 100%;
    position: fixed;
    top: 0;
    color: #fff;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    padding: 5px 20px;
    align-items: center;
    z-index: 10;
    box-shadow: 1px 1px 0px 3px #FD2155,
        2px 2px 0px 5px #08FDD8;
}

.sidebar_heading {
    font-size: 25px;
    padding: 20px 15px;
    padding-bottom: 0px;
    margin-bottom: 15px;
}

.sidebar_labels {
    display: none;
}

.sidebar_label {
    display: flex;
    padding: 5px 8px;
    cursor: pointer;
    transition: all 0.5s ease;
    align-items: center;
    transition: all 0.5s ease;
    margin: 2px;
    height: fit-content;
    justify-content: center;
    align-items: center;
    border-radius: 3px;
}

.sidebar_label:hover {
    background-color: #FD2155;
}

.sidebar_label_active {
    background-color: #FD2155;
}

.sidebar_label_icon {
    margin-right: 10px;
    font-size: 20px;
    margin-top: 3px;
}

.nodata {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    color: #fff;
    margin-top: 10px;
}

.nodata_img {
    width: 200px;
    height: 200px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.sidebar .nav {
    top: 15px;
    right: 12px;
    display: block;
}

@media screen and (min-width: 575px) {
    .sidebar .nav {
        display: none;
    }
    .sidebar_labels {
        display: flex;
        align-items: center;
    }
}

@media screen and (min-width: 800px) {
    .sidebar {
        flex-direction: row;
    }
}