.login_page {
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login_container{
    display: flex;
    flex-direction: column;
    background-color: #161623;
    padding: 20px 30px;
    border-radius: 3px;
}

.login_container .heading{
    font-size: 35px;
    margin-bottom: 10px;
    color: #fff;
}

.login_container input{
    display: block;
    margin: 10px 0px;
    border: 2px solid transparent;
    border-radius: 3px;
    width: 220px;
    padding: 5px 10px;
}

.login_container input[type="submit"]{
    width: fit-content;
    cursor: pointer;
    transition: all 0.5s ease;
}

.login_container input[type="submit"]:hover{
    background-color: #d2caff;
}