/* @import url('https://fonts.googleapis.com/css2?family=Pacifico&family=Sono:wght@200;400;600;700;800&display=swap'); */

@import url('https://fonts.googleapis.com/css2?family=Alkatra:wght@400;500&family=Sono:wght@200;400;600;700;800&display=swap');

.portfolio_page {
    display: flex;
}

.sidebar_container {
    width: 180px;
    display: none;
}

.main_container {
    width: 100%;
    margin: auto;
}

.nav {
    right: 40px;
    top: 40px;
    color: #fff;
    font-size: 30px;
    cursor: pointer;
    padding: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #0000009e;
    z-index: 10;
    position: fixed;
}

.hero {
    width: 100%;
    height: 80vh;
    position: relative;
    user-select: none;
    cursor: default;
}

.hero_center {
    padding-top: 80px;
    padding-left: 20px;
    padding-right: 20px;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    height: fit-content;
    width: 100%;
    left: 0;
    right: 0;
}


.hero_center .special_char {
    color: #FD1056;
    text-shadow: -3px 2px #08FDD8;
    font-size: 45px;
    animation: wobble 1s ease;
}

.hero_center p {
    font-size: 40px;
    font-weight: 700;
    margin-bottom: 5px;
    color: #fff;
    width: fit-content;
}

.hero_center p.small {
    font-size: 12px;
    font-weight: 400;
    line-height: 10px;
    margin-bottom: 30px;
    margin-top: 20px;
}

.hero_center .contact {
    border: 1px solid #08FDD8;
    background-color: transparent;
    padding: 8px 12px;
    cursor: pointer;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #08FDD8;
    transition: all 0.8s ease;
    font-size: 20px;
}

.hero_center .contact::before {
    content: '';
    background-color: #08FDD8;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    transform: scaleX(0);
    transition: all 0.5s ease;
    transform-origin: 0 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.hero_center .contact:hover {
    color: #000;
}

.hero_center .contact:hover::before {
    transform: scaleX(1);
}

.type {
    display: inline-block;
    border-right: 2px solid #fff;
    white-space: nowrap;
    overflow: hidden;
    width: fit-content;
    animation:
        typing 2s steps(14),
        cursor .4s step-end infinite alternate;
    animation-delay: 0.2s;
}

@keyframes cursor {
    50% {
        border-color: transparent;
    }
}

@keyframes typing {
    from {
        width: 0;
    }

    to {
        width: 100%;
    }
}

.special_char:hover .rubberband {
    display: inline-block;
    animation: rubberband 1s ease;
    cursor: pointer;
}

@keyframes rubberband {
    0% {
        transform: scaleX(1);
    }

    20% {
        transform: scaleX(1.2) scaleY(0.8);
    }

    40% {
        transform: scaleX(0.8) scaleY(1.2);
    }

    60% {
        transform: scaleX(1.1) scaleY(0.9);
    }

    100% {
        transform: scaleX(0.9) scaleY(1.1);
    }
}

.container {
    width: 100%;
    padding-left: 20px;
    padding-right: 20px;
    margin: auto;
    position: relative;
}

.heading_live {
    font-size: 50px;
    margin-top: 80px;
    /* font-family: 'Pacifico', cursive; */
    font-family: 'Alkatra', cursive;
    letter-spacing: 2px;
    margin-bottom: 20px;
    color: #c2fef5;
    /* set the text color to white */
    text-shadow: 0 0 30px #fff, 0 0 40px #08FDD8, 0 0 70px #08FDD8, 0 0 100px #08FDD8;
    /* create a series of text shadows with increasing blur and neon color */
}

.para {
    color: #fff;
    font-size: 18px;
    z-index: 2;
}

.work {
    position: relative;
    width: 100%;
    overflow-x: hidden;
}

.heading_bold {
    color: #262626;
    font-size: 250px;
    font-weight: 800;
    position: absolute;
    top: -90px;
    right: -350px;
    z-index: -1;
}

.skill_sphere {
    color: #fff;
    display: flex;
    justify-content: center;
    user-select: none;
    cursor: default;
}

.tagcloud--item {
    cursor: default;
    transition: all 0.5s ease;
    color: #08FDD8;

}

.contact_container {
    color: #fff;
    padding: 40px 0px;
    position: relative;
}

.heading_stroke {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 56px;
    font-weight: bold;
    color: transparent;
    -webkit-text-stroke: 0.5px #08FDD8;
    text-stroke: 1px #08FDD8;
    margin-bottom: 40px;
}

.contact_form {
    width: 100%;
}

.contact_form form table {
    width: 100%;
    position: relative;
}

.contact_form form table::after {
    content: '@';
    position: absolute;
    bottom: 50px;
    left: 0;
    right: 0;
    font-weight: 700;
    color: #2B2B2B;
    height: fit-content;
    width: fit-content;
    margin: auto;
    z-index: -1;
    font-size: 200px;
}

.contact_form form table tr {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

* {
    box-sizing: border-box;
    scroll-behavior: smooth;
}

.contact_form form table tr td {
    width: 100%;
}

.contact_form form table .half td {
    width: 49.2%;
}

.contact_form form table tr td input,
.contact_form form table tr td textarea {
    width: 100%;
    border: none;
    padding: 8px 12px;
    margin: 4px 0px;
    background-color: #2B2B2B;
    outline: none;
}

.contact_form form table td {
    position: relative;
    height: fit-content;
    width: fit-content;
}

.contact_form table td::after {
    content: '';
    height: 2px;
    width: 100%;
    background-color: #08FDD8;
    position: absolute;
    bottom: 5px;
    left: 0;
    z-index: 2;
    transition: all 1s ease;
}

#ltd::after {
    background-color: transparent !important;
}

.contact_form form table tr td input:focus .contact_form form table tr td::after,
.contact_form form table tr td textarea:focus .contact_form form table tr td::after {
    width: 100%;
    right: 0;
}

.contact_form form table tr td textarea {
    height: 100px;
    resize: vertical;
}

#contact_btn {
    border: 1px solid #08FDD8 !important;
    background-color: transparent !important;
    padding: 8px 12px;
    cursor: pointer;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #08FDD8;
    transition: all 0.8s ease;
    font-size: 20px;
    width: fit-content !important;
    margin-top: 5px;
}

#contact_btn::before {
    content: '';
    background-color: #08FDD8 !important;
    position: absolute !important;
    top: 0;
    left: 0;
    width: 100% !important;
    height: 100% !important;
    z-index: -1;
    transform: scaleX(0) !important;
    transition: all 0.5s ease;
    transform-origin: 0 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

#contact_btn:hover {
    color: #000;
}

#contact_btn:hover::before {
    transform: scaleX(1) !important;
}

.sidebar_port {
    width: 160px;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: #181818;
    color: #fff;
    text-align: center;
    user-select: none;
    position: fixed;
    top: 0;
    left: 0;
}

.logo {
    background-color: #000;
    padding: 20px 0px;
    padding-top: 0px;
}

.logo span {
    display: block;
}

.special_char {
    color: #FD1056;
    text-shadow: -3px 2px #08FDD8;
    font-size: 100px;
    font-weight: 700;
    animation: wobble 1s ease;
    padding: 0;
    margin: 0;
    padding-top: 20px;
}

.name {
    font-weight: 800;
    font-size: 25px;
    padding: 0;
    margin: 0;
    margin-bottom: 10px;
}

.design {
    font-size: 12px;
}

.menu_items {
    display: flex;
    flex-direction: column;
}

.menu_items .item {
    padding: 20px 0px;
    border-bottom: 1px solid #2B2B2B;
    cursor: pointer;
    transition: all 0.5s ease;
}

.menu_items .item:hover {
    color: #08FDD8;
}

.social {
    padding: 20px 0px;
}

.social a {
    color: #fff;
    margin: 0px 5px;
    font-size: 18px;
    transition: all 0.5s ease;
}

.social a:hover {
    color: #08FDD8;
}

.side_by_side {
    display: flex;
    flex-direction: column;
}

.side_by_side_contact {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.contact_img {
    width: 400px;
    height: 400px;
    background-position: center;
    background-size: cover;
    display: none;
    animation: updown 5s ease infinite;
    transition: all 5s ease;
}

.model_container {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: #181818;
    color: #fff;
    text-align: center;
    user-select: none;
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    transform: translateY(-100%);
    transition: all 0.8s ease;
}

.model {
    position: relative;
}

.close_btn {
    position: absolute;
    color: #fff;
    top: 20px;
    right: 20px;
    font-size: 40px;
    font-weight: 700;
    transition: all 0.5s ease;
    display: flex;
}

.close_btn:hover {
    color: #FD1056;
    cursor: pointer;
}

.project_card {
    width: 33.3%;
    height: 180px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    filter: opacity(0.8);
    overflow: hidden;
    cursor: pointer;
    transition: all 0.5s ease;
}

.project_card::after {
    content: 'View';
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    height: 100px;
    width: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: auto;
    background-color: #00ffd9;
    border-radius: 50%;
    transform: scale(0);
    transition: all 0.5s ease;
}

.project_card:hover {
    filter: opacity(1);
}

.project_card:hover.project_card::after {
    transform: scale(1.1);
}

.works_container {
    display: flex;
    flex-wrap: wrap;
    margin-top: 100px;

}

.project_model_container {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    background-color: #181818e3;
    text-align: center;
    user-select: none;
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    transition: all 0.8s ease;
    align-items: center;
    justify-content: center;
}

.project_model {
    width: 300px;
    height: 400px;
    background-color: #fff;
    position: relative;
    border-radius: 10px;
    transition: all 0.5s ease;
}

.project_model_image {
    width: 300px;
    height: 200px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 10px;
    border-bottom-right-radius: 0px;
    border-bottom-left-radius: 0px;
}

.project_model_name {
    font-size: 30px;
    text-align: left;
    padding: 10px 20px;
    text-transform: capitalize;
}

.proj_desc {
    text-align: left;
    padding: 10px 20px;
    font-style: italic;
    padding-bottom: 0px;
    height: 90px;
    overflow-y: auto;
}

.proj_desc::-webkit-scrollbar{
    width: 8px;
}

.proj_desc::-webkit-scrollbar-track {
    background-color: #f2f2f2;
}

.proj_desc::-webkit-scrollbar-thumb {
    background-color: #ccc;
}

.proj_checkout_btn {
    position: absolute;
    bottom: 0;
    padding: 10px;
    background-color: #FD1056;
    color: #fff;
    left: 0;
    right: 0;
    cursor: pointer;
    border-radius: 10px;
    border-top-right-radius: 0px;
    border-top-left-radius: 0px;
    transition: all 0.5s ease;
}

.proj_checkout_btn:hover {
    opacity: 0.8;
}

.proj {
    top: -20px;
    right: -20px;
    width: 30px;
    height: 30px;
    background-color: #FD1056;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
}

.proj:hover{
    color: #fff;
    opacity: 0.8;
    cursor: pointer;
}

@keyframes updown {
    0% {
        transform: translateY(0px)
    }

    10% {
        transform: translateY(-5px)
    }

    25% {
        transform: translateY(-10px)
    }

    40% {
        transform: translateY(-5px)
    }

    50% {
        transform: translateY(0px)
    }

    65% {
        transform: translateY(5px)
    }

    75% {
        transform: translateY(10px);
    }

    85% {
        transform: translateY(5px)
    }

    100% {
        transform: translateY(0px)
    }
}


@media screen and (min-width: 500px) {
    .hero_center p {
        font-size: 60px;
    }

    .hero_center .special_char {
        font-size: 75px;
    }
}

@media screen and (min-width: 700px) {
    .project_card {
        width: 25%;
    }
}

@media screen and (min-width: 950px) {
    .side_by_side_contact {
        flex-direction: row;
    }

    .contact_img {
        display: block;
    }

    .contact_form {
        width: 50%;
    }
}

@media screen and (min-width: 850px) {
    .hero_center p {
        font-size: 70px;
    }

    .hero_center .special_char {
        font-size: 85px;
    }

    .hero_center {
        padding-left: 40px;
        padding-right: 40px;
    }

    .container {
        padding-left: 40px;
        padding-right: 40px;
    }
}

@media screen and (min-width: 1100px) {

    .hero_center p {
        font-size: 80px;
    }

    .hero_center .special_char {
        font-size: 95px;
    }

    .side_by_side {
        flex-direction: row;
        justify-content: space-between;
    }

    .sidebar_container {
        display: block;
    }

    .model_container {
        display: none;
    }

    .nav {
        display: none;
    }
}